    width: 1220px;
  }
  .about__title {
    margin-top: 73px;
  }
  .about__item {
    width: 245px;
    padding-right: 0;
  }
}
.page-footer {
  padding: 0 20px;
  background-color: #f2f2f2;
  text-align: center;
}
.page-footer__logo {
  display: block;
  padding: 39px 0 30px 0;
  border-bottom: 1px solid #d9d9d9;
}
.page-footer__wrapper {
  width: 280px;
  margin: 0 auto;
}
.page-footer__social {
  border-bottom: 1px solid #d9d9d9;
}
@media (min-width: 768px) {
  .page-footer__wrapper {
    width: 708px;
    display: flex;
    justify-content: space-between;
    align-items: center;
  }
  .page-footer__logo {
    padding: 64px 0 62px 0;
    border-bottom: none;
  }
  .page-footer__logo-image {
    margin: 0;
  }
  .page-footer__social {
    border-bottom: none;
    margin-left: 18px;
  }
}
@media (min-width: 1300px) {
  .page-footer__wrapper {
    width: 1220px;
  }
}
.copyright {
  display: flex;
  justify-content: space-between;
  align-items: center;
}
.copyright__link:hover .copyright__icon,
.copyright__link:focus .copyright__icon {
  fill: #68b738;
}
.copyright__link:active {
  opacity: 0.3;
}
.copyright__icon {
  margin: 22px 0;
  fill: #666666;
}
.copyright__text {
  font-family: "Arial", sans-serif;
  color: #444444;
  line-height: 16px;
  font-size: 16px;
}
@media (min-width: 768px) {
  .copyright {
    width: 163px;
  }
}
.social__list {
  list-style: none;
  margin: 0;
  padding: 0;
  display: flex;
  justify-content: center;
  align-items: center;
}
.social__link {
  display: flex;
  margin: 22px 0;
}
.social__link:hover .social__icon,
.social__link:focus .social__icon {
  fill: #68b738;
}
.social__link:active {
  opacity: 0.3;
}
.social__icon {
  fill: #666666;
}
.social__list li:nth-child(2) {
  margin-right: 40px;
  margin-left: 37px;
}
@media (min-width: 768px) {
  .social__icon--vkontakte {
    width: 30px;
    height: 16px;
  }
  .social__icon--instagram {
    width: 23px;
    height: 23px;
  }
  .social__list li:nth-child(2) {
    margin-right: 28px;
    margin-left: 28px;
  }
  .social__icon--facebook {
    width: 14px;
    height: 28px;
  }
}
.catalog__list {
  list-style: none;
  margin: 0;
  margin-top: 40px;
  padding: 0;
}
.catalog__item {
  margin-bottom: 24px;
  padding: 0 20px;
  border-top: 1px solid #e6e6e6;
}
@media (min-width: 768px) {
  .catalog {
    width: 708px;
    margin: 0 auto;
  }
  .catalog .catalog__item:last-child {
    background-color: #ffffff;
    border: 1px solid #e6e6e6;
    padding-top: 25px;
  }
  .catalog__list {
    padding: 0;
    display: flex;
    flex-wrap: wrap;
    justify-content: space-between;
    margin-top: 25px;
  }
  .catalog__item {
    width: 324px;
    text-align: center;
    box-sizing: border-box;
    margin-top: 140px;
    margin-bottom: 3px;
  }
}
@media (min-width: 1300px) {
  .catalog {
    width: 1220px;
  }
  .catalog__item {
    width: 245px;
  }
}
.product {
  position: relative;
}
.product__image {
  position: absolute;
  top: 0;
  left: 20px;
}
.product__name {
  margin: 21px 0 0 140px;
  font-size: 16px;
  line-height: 20px;
  font-weight: normal;
  width: 75px;
  text-transform: uppercase;
}
.product__data {
  border-collapse: collapse;
  font-family: "Arial", sans-serif;
  font-size: 12px;
  line-height: 16px;
  color: #444444;
  margin: 13px 0 0 140px;
  width: 140px;
}
.product__value {
  padding: 0;
  text-align: right;
}
.product__prop {
  padding: 0;
  text-align: left;
}
.product__button {
  width: 280px;
  font-size: 16px;
  line-height: 20px;
  padding: 10px 0;
  margin: 22px 0 0 0;
}
@media (min-width: 768px) {
  .product {
    background-color: #f2f2f2;
    border: none;
    display: flex;
    flex-direction: column;
    align-items: center;
    padding-bottom: 40px;
    padding-top: 163px;
  }
  .product__image {
    left: 0;
  }
  .product__image--small {
    width: 324px;
    height: 172px;
    top: -47px;
  }
  .product__image--big {
    width: 324px;
    height: 208px;
    top: -82px;
  }
  .product__name {
    margin: 0;
    width: 130px;
    font-size: 20px;
  }
  .product__data {
    margin: 20px 0 0 0;
    width: 249px;
    font-size: 16px;
  }
  .product__data tr {
    border-bottom: 1px solid #cdcdcd;
  }
  .product__button {
    width: 249px;
    margin-top: 33px;
  }
  .product__prop {
    padding: 7px 0;
  }
}
@media (min-width: 1300px) {
  .product {
    margin-top: 142px;
  }
  .product__data {
    width: 169px;
  }
  .product__button {
    width: 169px;
  }
}
.show-more {
  display: flex;
  flex-direction: column;
  align-items: center;
}
.show-more__icon {
  width: 31px;
  height: 31px;
  background-image: url("../img/icon-plus-mobile.svg");
  margin: 26px 0 0 0;
}
.show-more__title {
  font-size: 16px;
  line-height: 20px;
  text-transform: uppercase;
  margin: 22px 0 0 0;
}
.show-more__subtitle {
  font-family: "Arial", sans-serif;
  font-size: 12px;
  line-height: 16px;
  color: #444444;
  margin: 8px 0 0 0;
}
.show-more__button {
  background-color: #f2f2f2;
  width: 280px;
  color: #444444;
  font-size: 16px;
  line-height: 20px;
  padding: 10px 0;
  margin: 21px 0 0 0;
}
@media (min-width: 768px) {
  .show-more {
    background-color: #ffffff;
  }
  .show-more__icon {
    margin-top: 55px;
  }
  .show-more__title {
    font-size: 20px;
    width: 135px;
    margin-top: 50px;
  }
  .show-more__subtitle {
    font-size: 16px;
    margin-top: 28px;
    width: 167px;
  }
  .show-more__button {
    margin-top: 85px;
    width: 249px;
  }
}
@media (min-width: 1300px) {
  .show-more__button {
    width: 169px;
  }
}
body {
  font-family: "Oswald-Regular", "Arial", sans-serif;
  font-size: 14px;
  color: #111111;
}
img {
  max-width: 100%;
  height: auto;
}
